<template>
    <div class="xinagqin">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="item in insuranceInfo.imgs" :key="item">
            <img :src="item" alt="">
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
      <div class="fw_list">
          <ul >
            <li v-for="(item,index) in list" :key="index" :class="choose==index?'typeChoose':''" @click="choose=index">
              {{item.title}}
            </li>
          </ul>
      </div>
      <div class="content_wrap"  >
        <div v-show="choose===0" v-html="insuranceInfo.content"></div>
        <div v-show="choose===1" v-html="insuranceInfo.problems"></div>
        <div v-show="choose===2" v-html="insuranceInfo.core"></div>
        <div v-show="choose===3" v-html="insuranceInfo.case"></div>
        <div v-show="choose===4" v-html="insuranceInfo.process"></div>
      </div>
      <div class="others">
        <div class="otherWrap">
          <h3>可选补充保险</h3>
          <ul>
            <li v-for="(item,index) in otherlist" :key="index" @click="redictNow(item.id)">
              <div class="img_wrap">
                <img :src="item.imgs" alt="">
              </div>
              <p>{{item.title}}</p>
              <div class="person">已预约 <span>{{item.yu_num}}</span>人</div>
            </li>
          </ul>
        </div>

      </div>
      <van-button type="default btn_yuy" @click="toOrderItem">预约</van-button>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
  import Swiper from 'swiper'
import { apiGet } from '../common/js/api';
    export default {
      name: "xiangqin",
      data(){
          return{
            choose:0,
            title:'',
            list:[
              {
                "title":"产品详情",
                "content":"<div><span>1</span><img src='http://thirdqq.qlogo.cn/qqapp/1106875417/729BE4F28FC1370DE13B82A2380420DA/40' alt=''></div>"
              },
              {
                "title":"常见问题",
                "content":"<div><span>2</span><img src='http://thirdqq.qlogo.cn/qqapp/1106875417/729BE4F28FC1370DE13B82A2380420DA/40' alt=''></div>"
              },
              {
                "title":"核心条款",
                "content":"<div><span>3</span><img src='http://thirdqq.qlogo.cn/qqapp/1106875417/729BE4F28FC1370DE13B82A2380420DA/40' alt=''></div>"
              },
              {
                "title":"投保案例",
                "content":"<div><span>4</span><img src='http://thirdqq.qlogo.cn/qqapp/1106875417/729BE4F28FC1370DE13B82A2380420DA/40' alt=''></div>"
              },
              {
                "title":"理赔案例",
                "content":"<div><span>5</span><img src='http://thirdqq.qlogo.cn/qqapp/1106875417/729BE4F28FC1370DE13B82A2380420DA/40' alt=''></div>"
              }

            ],
            otherlist:[
              {
                "img": require('./img/timg.jpg'),
                "p":"龙熙科技",
                "num":"999",
                id:1
              },
              {
                "img": require('./img/timg.jpg'),
                "p":"龙熙科技",
                "num":"999",
                id:2
              },
              {
                "img": require('./img/timg.jpg'),
                "p":"龙熙科技",
                "num":"999",
                id:3
              },
            ],
            insuranceInfo:{
              id:0
            }
          }
      },
      created(){
        this.getDetail();
      },
      mounted(){
          let{ id,title }=this.$route.query;
          this.title=title;
          
          this.id=id;
      },
      methods:{
        //获取其他相关保险列表
        getOtherList(){

        },
        //获取保险详情
        getDetail(){
          apiGet('/insurance/ins-detail',{
            ins_id:this.$route.query.id
          }).then(res=>{
            this.insuranceInfo = res.data.data.detail;
            this.otherlist = res.data.data.other;
            document.title= this.insuranceInfo.title;
            this.$nextTick(()=>{
              var mySwiper = new Swiper('.swiper-container', {
                autoplay: true,//可选选项，自动滑动
                loop : true,
                pagination: {
                  el: '.swiper-pagination',
                },
              })
            })
          })
        },
        //点击其他保险
        redictNow(id){
          this.$router.replace({
            path:'/xiangqin',
            query:{
              id
            }
          })
          this.getDetail();
        },
        //跳转到预约页面
        toOrderItem(id){
          this.$router.push({
            path:"/yuyue",
            query:{
              id:this.insuranceInfo.id
            }
          })
        }
      }
    }
</script>
<style>
  .xinagqin .content_wrap p{
    font-size: 0.14rem;
  }
  .xinagqin .content_wrap img{

    width:100% !important;
  }
</style>

<style scoped lang="scss">
  .swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{bottom:0}
  .kefu{
    width: 0.52rem;
    height: 0.52rem;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
    border-radius:50%;
    box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
    position: fixed;
    bottom: 1.5rem;
    display: block;
    text-align: center;
    line-height: 0.52rem;
    font-size: 0.28rem;
    color: white;
    z-index: 3;
    right: 0.11rem;
  }
  .xinagqin {
    padding-top: 0.18rem;
    height: 100%;
    overflow: auto;
    background: #E6E9ED;
    .swiper-container{
      width: 93%;
      border-radius: 0.04rem;
      .swiper-slide>img{
        width: 100%;
        max-height: 2.56rem;
      }
    }
    .fw_list{
      width: 3.5rem;
      margin: 0.17rem auto 0;
      border-radius: 0.06rem;
      align-items: center;
      overflow: hidden;
      height:0.45rem;
      display: flex;
      white-space: nowrap;
      ul{
        border-radius:0.06rem;
        height:0.45rem; 
        background: #C4C8CF;
        overflow-x: auto;
        overflow-y: hidden;
        width:100%;
        &::-webkit-scrollbar{display: none};
      }
      li{
        width: 0.75rem;
        line-height: 0.3rem;
        text-align: center;
        font-size: 0.14rem;
        background: #C4C8CF;
        box-sizing: border-box;
        display: inline-block;
        color: white;
        height: 0.3rem;
        border-right: 1px solid #eee;
      }
      .typeChoose{
        background: white;
        color: #333;
        line-height: 0.46rem;
        height: 0.46rem;
        border-bottom: 1px solid white;
      }
    }
    .content_wrap{
      width: 3.5rem;
      min-height: 2.39rem;
      background: white;
      margin: 0 auto 0.17rem;
      border-radius: 0.04rem;
      div{
        width: 100%;
        p{
          width: 100%;
          img{
            width: 100% !important;
          }
        }
      }
    }

    .others{
      width: 3.5rem;
      border-radius: 0.04rem;
      margin: 0 auto;
     padding-bottom: 0.6rem;
      .otherWrap{
        padding-top: 0.14rem;
          background: white;
      }
      h3{
        width: 1.28rem;
        height: 0.31rem;
        background: #EEA95F;
        border-radius:0px 0.16rem 0.16rem 0;
        font-size: 0.15rem;
        font-weight: 400;
        line-height: 0.31rem;
        color: white;
        text-align: center;
      }
      ul{
        display: flex;
        flex-wrap: wrap;
        background: white;
        margin-bottom: 0.6rem;
        li{
          width: 1.56rem;
          margin-top: 0.13rem;
          margin-left: 0.13rem;
          .img_wrap{
            border:1px solid rgba(0,67,142,.1);
            width: 1.56rem;
            height: 1.59rem;
            border-radius:4px;
            img{
              width: 100%;
              height: 100%;
              border-radius:4px;
            }
          }
          p{
            font-size: 0.16rem;
            font-weight: 600;
            line-height: 0.3rem;
            color: #333;
          }
          .person{
            color: #999999;
            font-size: 0.13rem;
            line-height: 0.28rem;
          }

        }
      }
    }
    .btn_yuy{
      position: fixed;
      width: 100%;
      height: 0.47rem;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      color: white;
      text-align: center;
      line-height: 0.47rem;
      font-size: 0.17rem;
      bottom: 0;
    }
  }

</style>



